.main {
    max-width: 100vw;
  max-width: 1100px;
  border-radius: 0.4rem;
    margin: auto;
    padding-bottom: 1rem;
}

h1 {
    padding: 1rem;
}
.options {
  display: flex;
  margin: 1rem;
  background-color: cadetblue;
  border-radius: 0.4rem;
}

.options * {
    font-size: 1.2rem;

}
.options > div { 
  display: flex;
  flex-direction: column;
  margin: 0.5rem 1rem;
}
.options label {
  margin: 0.3rem;
}
.options > div > *:nth-child(2) {
  border: 1px solid blue;
  border: none;
  border-radius: 0.4rem;
  padding: 0.4rem;
  color: #555;
}
.table {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.5rem;
  margin: 1rem;
    padding: 0.2rem;
    padding-bottom: 1rem;
}

.card {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin:0.5 auto;
    max-width: 100%;
  font-size: 1rem;
  border-radius: 0.4rem;
  overflow: hidden;
}

.card > * {
}
.image {
    margin: 2.5% auto;
    height: 250px;
    width: 95%;
    border-radius: 0.4rem;
    overflow: hidden;
}
.card > div {
  align-items: center;
}

.title {
  color: darkgoldenrod;
}
.description {
    width: 95%;
    margin: 0.5rem;
    margin-bottom: 40px;
}

.date {
    position: absolute;
    bottom: 10px;
    right: 10px;
    margin: 0;
    color: #888a;
}

@media (max-width: 980px) {
  .table {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .table {
    display: flex;
        flex-direction: column;
        align-items: center;
  }
  .options {
    flex-direction: column;
  }
    /* .card {
      font-size: 0.8rem;
    } */
}

@media (max-width: 500px) {
    .table {
        margin: 0.1rem;
        paddin:0;
    }
    .card:last-child{
        margin-bottom: 2rem;
    }
}
