.themeContainer {
    margin: 1rem;
    background-color: #777;
    height: 15px;
    width: 45px;
    border-radius: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;;
    cursor: pointer;
}

.slider {
    height: 15px;
    width: 15px;
    border-radius: 50%;
}

.themetext {
    margin: 0;
    padding: 0;
    font-size: 8px;
}

