.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 1100px;
  margin: auto;
  overflow:hidden;
  border-radius: 0.4rem;
}
.options {
  display: flex;
  margin: 1rem;
  background-color: cadetblue;
  border-radius: 0.4rem;
}

.options > div { 
  display: flex;
  flex-direction: column;
  margin: 0.5rem 1rem;
}
.options label {
  margin: 0.3rem;
}
.options > div > *:nth-child(2) {
  border: 1px solid blue;
  font-size: 1.2rem;
  border: none;
  border-radius: 0.4rem;
  padding: 0.4rem;
  color: #555;
}
.table {
  /* border: 1px solid red; */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0.5rem;
  margin: 1rem;
  margin-bottom: 3rem;
  width: 98%;
}

.row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  /* border: 1px solid yellow; */
  width: 98%;
  font-size: 0.8rem;
  border-radius: 0.4rem;
  overflow: hidden;
}

.row > div {
  margin: 0;
  padding: 0.2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  align-items: center;
  /* border: 1px solid maroon; */
}

.name {
  color: darkgoldenrod;
}
.hex {
}
.rgb {
}
.box {
}

@media (max-width: 980px) {
  .table {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .table {
    grid-template-columns: repeat(1, 1fr);
  }
  .options {
    flex-direction: column;
  }
}

