a,a:visited {
  text-decoration: none;
  color: inherit;
}

body {
  background-color: #000;
}

.container {
  min-height: 100vh;
  width: 100%;
  margin: 0.1rem;
  margin-bottom: 0;
  color: #aaa;
}

.smallscreen {
}
.nav {
  position: relative;
  height: 50px;
  width: 99%;
  max-width: 1100px;
  margin: 0.5rem auto 2rem;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 3px 20px rgba(255,255,255,0.1);
  z-index: 6;
}

.nav ul {
  display: flex;
  padding:0;
  justify-content: flex-start;
}

.nav li {
  list-style: none;
  margin: 0.5rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.nav li:first-child {
  margin-left: 1.5rem;
}

.nav li:hover {
  color: dodgerblue;
  box-shadow: 0 0 0 1px dodgerblue;
}

.controls {
  position: sticky;
  margin: 0.2rem 1rem;
  border-radius: 10px;
}
.close {
  font-size: 2rem;
  transform: rotate(45deg);
  position: absolute;
  top: 3px;
  right:6px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}
.close:hover {
  color: maroon;
  transform: rotate (45deg) scale(1.05);
}

.footer {
  z-index: 5;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 99%;
  max-width: 1100px;
  font-size: 0.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 0.3rem;
  border-radius: 0.4rem;
}
.footer span {
  margin: 0 0.3rem;
}
.footer span:is(:nth-of-type(4), :nth-of-type(6)) {
  cursor: pointer;
}

